*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.mainDiv{
  width: 100%;
  height: 100vh;
  background: rgb(176, 197, 197);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.centerDiv{
  background-color: white;
  width: 55%;
  height: 90vh;
  box-shadow: 5px 5px 25px -5px black;
  border-radius: 1.5rem;
  overflow:scroll;
}


h1{
  color: white;
  background: transparent;
  background-color: grey;
  padding: 1rem 0 1rem 0;
  box-shadow: 5px 5px 15px -5px rgb(16, 20, 70);
  border-radius: 1rem;
  margin: 0 .3rem;
}

input{
  text-align: center;
  height: 3rem;
  width: 60%;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  outline:none;
  border-bottom : 2px solid grey;
}

ol{
  margin-top: 2rem;
}

ol li{
  padding-left: 0;
  text-align: left;
  font-size: 1.5rem;
  font-weight: 500;
  min-height: 1rem;
  display: flex;
  align-items: center;
  color:gray;
  text-transform: capitalize;
}

.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .fa-times{
  width: 1.75rem;
  height: 1.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(146, 137, 137);
  border-radius: 50%;
  padding: 0.85rem;
  margin-right: 0.5rem;
  color: rgb(248, 239, 239);
  box-shadow: 5px 5px 15px -5px black;
}

.btn:hover{
  background-color: rgb(56, 148, 56);
}
.todo_style .fa-times:hover{
  color: white;
  background-color: rgb(230, 47, 47);
  cursor: pointer;
}

@media screen and (max-width:700px) {
  .centerDiv{
    width: 85%;
    height: 90vh;
  }
  
  .todo_style .fa-times{
    width: 1.5rem;
    height: 1.5rem;
  }

}